				"html": "<p>After an Apple Phone IOS update, there was an issue with email links on several content websites when customers would click the <strong>email share link</strong>. When clicking on the email share, customers were being shown an alert box. </p>\n<p>On desktop devices, there was no issue. </p>\n<p>After going through various debugging techniques, I noticed 2 things that may help other developers out:</p>\n<ul>\n<li>Make sure that there is no target attribute on the email share link. This is what initiates alerts on mobile devices.</li>\n<li>When debugging errors with email share links on Device Simulators, remember that similators like the IOS Simulator don't have email clients installed initially.</li>\n</ul>\n<p>Here's a code sample of something that works:</p>\n<pre><code class=\"language-html\">  &#x3C;a href=\"mailto:?subject={{articleTitle}}&#x26;amp;body={{articleLink}}\" data-track-share=\"Email\" data-track-slug=\"{{articleLink}}\" class=\"share__link share__link--mail js-share-mail js-share-event\" title=\"Email\">Share&#x3C;/a>\n</code></pre>",
